import React from "react"
import { Layout, message, Table, Tag, Popconfirm } from "antd"
import TopNavbar from "../navbar/top-navbar"
import LeftNavbar from "../navbar/left-navbar"
import PageFooter from "../footer"
import moment from "moment"
import { useQuery, useMutation } from "@apollo/react-hooks"

// ===== Query and Mutation Section =====
import { GET_PAYMENTS } from "../../graphql/query"
import { DELETE_POST } from "../../graphql/mutation"

const { Content } = Layout

function Payment() {
  // ===== Mutation Varile Section =====
  const [deletePost] = useMutation(DELETE_POST)

  const columns = [
    {
      title: "First Name",
      dataIndex: "fname"
    },
    {
      title: "Last Name",
      dataIndex: "lname"
    },
    {
      title: "Email",
      dataIndex: "email"
    },
    {
      title: "Phone Number",
      dataIndex: "phoneNumber"
    },
    {
      title: "Product",
      dataIndex: "product"
    },
    {
      title: "Price",
      dataIndex: "price"
    },
    {
      title: "Cancle",
      dataIndex: "cancle"
    },
    {
      title: "Actions",
      dataIndex: "action"
    }
  ]

  const DisplayPost = () => {
    const { error, loading, data } = useQuery(GET_PAYMENTS)
    if (error) console.log(error)
    if (loading) return <Table loading={true}></Table>
    if (data) {
      console.log(data)

      const DisplayTable = () => {
        return (
          <Table
            columns={columns}
            dataSource={data.payments.map((post) => {
              const {
                id,
                fname,
                lname,
                email,
                created_at,
                phoneNumber,
                price,
                product,
                cancle
              } = post
              return {
                key: id,
                fname,
                lname,
                email,
                phoneNumber,
                product: product.map((data) => {
                  return <Tag color="blue">{data}</Tag>
                }),
                price: `$${price}.00`,
                cancle: cancle ? (
                  <Tag color="red">Yes</Tag>
                ) : (
                  <Tag color="green">No</Tag>
                ),
                created_at: moment.unix(created_at / 1000).format("YYYY-MM-DD"),
                action: (
                  <div>
                    <Popconfirm
                      placement="topRight"
                      title="Are you sure to delete this Post?"
                      okText="Yes"
                      cancelText="No"
                      onConfirm={() => {
                        deletePost({ variables: { id: `${id}` } })
                        message.success("The Post has been Deleted")
                      }}
                    >
                      <Tag color="#f50" className="btn">
                        Delete
                      </Tag>
                    </Popconfirm>
                  </div>
                )
              }
            })}
            pagination={true}
          />
        )
      }
      return (
        <div>
          <DisplayTable />
        </div>
      )
    }
  }

  return (
    <Layout style={{ minHeight: "100vh" }}>
      {/* =========Left Navbar ======= */}
      <LeftNavbar />
      <Layout>
        <Content>
          {/* =========Top Navbar ======= */}
          <TopNavbar />

          <div className="koompi container">
            {/* ======= Display content ====== */}

            <div className="background_container">
              <h1 className="title_new_post">Payments</h1>
              <DisplayPost />
            </div>
          </div>
        </Content>
        <PageFooter />
      </Layout>
    </Layout>
  )
}

export default Payment
